/**
 * Template: Collection
 * ------------------------------------------------------------------------------
 * Highly coupled scripts to power the collection page functionality.
 *
 * @namespace collection
 */
import { load } from '@shopify/theme-sections';
import breakpoints from '~/helpers/breakpoints';
import Accordion from '~/components/accordion';
import Toggle from '~/components/toggle';
import '~/sections/recommended-products';
import '../sections/block-50-50';
import '../sections/image-video-hero';
document.addEventListener('DOMContentLoaded', () => {
  load('recommended-products');
  load('block-50-50');
  load('image-video-hero');
  Toggle({
    toggleSelector: 'filterMenu',
    overlay: false,
    toggleTabIndex: false
  }).init();
  Accordion('#filterMenu', {
    singleOpen: false,
    expandAll: true,
    responsive: [{
      breakpoint: breakpoints.medium,
      settings: {
        showAll: true
      }
    }]
  }).init();
});
document.addEventListener('shopify:section:load', () => {
  load('*');
  Toggle({
    toggleSelector: 'filterMenu',
    overlay: false,
    toggleTabIndex: false
  }).init();
  Accordion('#filterMenu', {
    singleOpen: false,
    expandAll: true,
    responsive: [{
      breakpoint: breakpoints.medium,
      settings: {
        showAll: true
      }
    }]
  }).init();
});